
.nav {
    display: flex;
    align-items: center;
    padding-left: 1em;
    padding-right: 1em;
    line-height: 3em;
    width: 100%;
    cursor: pointer;
}

.nav_sub {
    height: 0;
    overflow: hidden;
}

.active_menu.active .nav > svg {
    transform: rotate(180deg);
}

.nav_sub.active {
    height: auto;
    padding: .5em 2em;
}

.nav_sub a {
    padding-left: 1em;
    color: #fff;
    line-height: 2.5em;
    position: relative;
}

.nav_sub a::after {
    content: '';
    width: 5px;
    height: 5px;
    background-color: #3B73D1;
    display: block;
    border-radius: 100%;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
}

.nav.active {
    background-color: #3B73D1;
}

.btn {
    background-color: var(--bg--sub--color);
    border: 0;
    line-height: 4em;
    display: flex;
    justify-content: center;
    align-items: center;
}

.main_tab_head .btn_tab {
    display: block;
    background-color: #DFDFDF;
    line-height: 3em;
    padding: 0 2em;
    margin-left: 1.2em;
    color: #fff;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    color: #A6A6A6;
}

#main_tab1:checked~.main_tab_head .btn_tab[for="main_tab1"] {
    background-color: #1B3262;
    color: #fff;
}


