.title {
    display: flex;
    align-items: center;
}

.table-data {
    width: 100%;
    border-collapse: collapse;
    margin: 2em 0;
}

.table-data thead th {
    background-color: var(--bg--main--color);
    color: #fff;
    line-height: 3em;
}

.table-data tbody td {
    background-color: #fff;
    color: #000;
    line-height: 3em;
    border-bottom: 1px solid #E2E2E2;
    padding: 0 2em;
}


.table-data tbody tr:nth-child(2n + 2) td {
    background-color: #F4F6FA;
    color: #000;
}

.table-data-pageing {
    display: flex;
    align-items: center;
    justify-content: center;
}

.table-data-pageing li {
    padding: 0 10px;
    cursor: pointer;
}

.table-data-pageing .arrow-left  {
    width: 18px;
}

.table-data-pageing .arrow-right  {
    width: 18px;
}


.project-view {
    background-color: #fff;   
    padding: 2em;
    border-radius: 10px;
}

/* from controller style */


textarea.from_controller {
    width: 100%;
    background-color: #F6F6F6;
    border: 1px solid #DEDEDE;
    padding: 2em;
    height: 10em;
}

.project-view__reply {

}


.project-view__reply > li {
    padding: 0 1em;
    border-left: 3px solid var(--bg--main--color);
    margin-top: 2em;
}

.project-view__reply__title {
    display: flex;
    align-items: center;
}

.project-view__reply__contents {
    font-size: 1em;
    color: #A6A6A6;
}

.employee-view {
    background-color: #fff;   
    padding: 2em;
    border-radius: 10px;
    height: 100vh;
}


.box-bor {
    background-color: #fff;
    box-shadow: 1px 1px 10px rgba(0,0,0,0.12);
    border-radius: 1em;
    padding: 2em;
}